 *{
  padding:0;
  /* box-sizing: border-box; */
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header{
  min-height: 60px;
  background: crimson;
  color:whitesmoke;
  overflow: hidden;
display: flex;
justify-content:space-between;
align-items: center;
flex-wrap: wrap;

}

header .logo{
  margin-left: 50px;
  text-transform: uppercase;
  letter-spacing: 2px;;
  float: left;
  padding-top:10px;
}
header ul{
 
  text-align: right;
  list-style: none;
  margin-right: 50px;
}

header ul li{
  margin-top: 15px;
  display: inline-block;
}

header ul li a{
 
  text-decoration:none;
  color:whitesmoke;
  text-transform: uppercase;
  opacity: 0.8;
  padding: 0 10px;
}
 header ul li a:hover{
   opacity:1;

 } 


 section{
   min-height: 100vh;
   display:flex;
   justify-content: center;
   align-items: center;

 }

 @media(max-width :750px){
   header .logo,
   header ul{
     width:100%;
     margin:0;
     text-align: center;
     line-height: 50px;
     
   }
  }
  @media(max-width:500px)
  {
    header ul{
      position: fixed;
      top:0;
      left:-100%;
      width:100%;
      background: crimson;
      height:100vh;
      display:flex;
      justify-content: space-around;
      align-items: center;
      flex-direction: column;
      z-index: 99;
      transition: 0.5s linear;
    }
    header ul.toggle{
      left:0;
    }
    header ul li{line-height:normal}
    header .menu,
    header .close{
      display:block;

    }
    header .logo{
      width:auto;
      width:initial;
      margin-left: 20px;
      line-height: 70px;
    }
    section h2{
      font-size: 1.4rem;
    }
  }
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}





